@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400&display=swap);
 .page_loader {
    width: 100%;
    height: 100%;
    background: var(--theme-color);
    color: #5c453f;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999999999 !important;
  }
  .loading-wrapper {    
    position: fixed;
    top: 50%;
    left: 50%;
    transform:translate(-50%,-50%);        
  }
  .loader-heart {
    position: absolute;
    opacity: 0;
    animation: pulse2 1.8s linear infinite;
  }
  .loader-heart1 {
    top: 0;
    left: 166px;
  }
  .loader-heart2 {
    width: 30px;
    top: 38px;
    left: 130px;
    animation-delay: 0.4s;
    -webkit-animation-delay: 0.4s;
    -moz-animation-delay: 0.4s;
  }
  .loader-heart3 {
    width: 20px;
    left: 173px;
    top: 78px;
    animation-delay: 0.8s;
    -webkit-animation-delay: 0.8s;
    -moz-animation-delay: 0.8s;
  }
  
  @keyframes pulse2 {
    0% {
      transform: translateY(5px) scale(0.8);
      opacity: 0;
    }
    30% {
        transform: scale(1) translateY(-10px);
        opacity: 0.8;
    }
    60% {
        transform: scale(0.8) translateY(-20px);
        opacity: 0;
    }
  }

  @media(max-width:575px){
    .loader-heart1,
    .loader-heart2,
    .loader-heart3 {      
      left: 0;
    }    
  }
/*---------------------
    HEADER CSS START
----------------------*/
.header {
    position: fixed;
    left: 0;
    width: 100%;
    z-index: 999;        
}
.fixed{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;    
    background-color:var(--theme-color);
    animation: smoothScroll 1s forwards;
    z-index:99999 !important;
}
@keyframes smoothScroll {
    0% {
        transform: translateY(-40px);
    }
    100% {
        transform: translateY(0px);
    }
}
.header .navbar-nav .nav-link {
    font-size: 16px !important;
    padding: 25px 20px !important;
    display: block !important;
    color: #fff !important;
    font-weight: 600 !important;
    position: relative !important;    
    text-transform: uppercase !important;
    transition: all 0.5s !important;
}
@media(max-width:1199px){
    .navbar-light .navbar-brand img{
        width: 50%;
    }
    .header .navbar-nav .nav-link{
        padding: 15px 20px !important;
    }
    .navbar  a.navbar-brand{
        width: 50%;
    }    
}
@media(max-width:991px){
    .navbar-light .navbar-toggler {
        border-color: rgba(0,0,0,0.5) !important;
        filter: invert(1) !important;
        font-size: 18px !important;
    }
    .header.menu_open{
        background-color:var(--theme-color);
        height:100%;
        z-index:999999999999999999999999 !important;
    }
    .header.fixed  a.navbar-brand{
        padding: 0;
        width: 40%;
    }
    .header.menu_open .navbar-nav{
        overflow-y: auto;
        height:100vh;        
        padding-bottom: 100px;
    }
}
@media(max-width:767px){
    .navbar-light .navbar-brand img {
        width: 60%;
    }
}
@media(max-width:575px){
    .navbar-light .navbar-brand img {
        width:140px;
    }
}
@media(max-width:420px){    
    .wedding_banner .wedding_announcement .wpo-wedding-date #clock > div {
        width: 135px;
        height: 95px;
    }
}
