  .wedding_banner {    
    height:20vh;    
  }
  .wedding_banner:after{
    content: '';    
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 25vh;
    background-color: rgba(0,0,0,0.1);
    z-index: 1;
  }
  .wedding_banner .wedding_announcement {            
    position: absolute;
    z-index: 2;
    text-align: center;
  }  
  .wedding_banner .wedding_announcement {
    width: 80%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }  
  .carousel-item{
    height:100vh;
  }
  .carousel-item img {
    height: 100%;
    object-fit: cover;
    object-position: top;
  }
