section.couple-section{
    position: relative;
}
section.couple-section .z_index{
    z-index:1;
}
.shape_1,
.shape_2{
    width: 400px;
    z-index:-1;
    position: absolute;
    top:0; 
}
.shape_1{      
    left:0;     
}
.shape_2{        
    right:0;     
}
.couple-section .middle-couple-pic {    
    position: relative;    
    max-width:80%;
    margin:0 auto;
}
.couple-section .middle-couple-pic .frame-img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 125%;
}

.couple-section h3 {    
    font-size: 22px;
    margin: 0 0 15px 0;
}
.couple-section .couple-img {
    margin-bottom: 20px;
    width: 125px;
    height: 125px;
    margin-left: auto;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    background-color: #fff;
