.love_story{
    position: relative;
}
.love_story .story-timeline {
    position: relative;
}

.love_story .story-timeline:after {
    content: "";
    background:#edf3f8;
    width: 2px;
    height: 100%;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
  }
  .love_story .story-timeline .round-shape {
    background: #fff;
    width: 20px;
    height: 20px;
    position: absolute;
    left: 50%;
    top: 0;
    border: 1px solid #738ea5;
    transform: translateX(-50%);
    border-radius: 50%;
    z-index: 1;
  }
  .love_story .story-timeline .round-shape:before {
    position: absolute;
    left: 2px;
    top: 2px;
    width: 14px;
    height: 14px;
    content: "";
    background: #86a0b6;
    border-radius: 50%;
  }
  .love_story .story-timeline .img-holder {
    max-width: 330px;
    width:330px;
    height:330px;
    padding: 10px;
    border: 1px dashed #86a0b6;
    border-radius: 50%;
    position: relative;
    z-index: 1;
    background: #fff;
    margin: 0 auto;
  }
  .love_story .story-timeline .img-holder:after {
    background: #fff;
    content: "";
    width: 40px;
    height: 350px;
    left: -100%;
    opacity: .2;
    position: absolute;
    top: 0;
    transition: all 1s cubic-bezier(0.19, 1, 0.22, 1);    
    z-index: 1;
  }
  .love_story .story-timeline .img-holder:hover:after {
    left: 100%;    
  }
  .love_story .story-timeline .img-holder:before {
    position: absolute;
    left: -140px;
    top: 195px;
    width: 90%;
    height: 90%;
    border: 1px dashed #738ea5;
    content: "";
    border-radius: 50%;
    z-index: -1;
    border-bottom: 0;
    border-right: 0;
    border-top: 0;
    transform: rotate(66deg);
  }
  .love_story .story-timeline .img-holder.left-site:before {
    position: absolute;
    left: auto;
    right: -140px;
    transform: rotate(102deg);
  }
  .love_story .story-timeline .img-holder > img {
    border-radius: 50%;
    width: 100%;
    height:100%;
    object-fit:cover;
    object-position:center center;
  }
  .love_story .story-timeline .img-holder .story-shape-img {
    position: absolute;
    bottom: -45px;
    left: 55px;
  }
  .love_story .story-timeline .img-holder .story-shape-img img {
    border-radius: 0;
  }
